<template>
  <div>
    <h3>Add Category</h3>
    <div>
      <form @submit.prevent="saveCat" class="form-control">
        <input type="text" v-model="cat_id" required class="form-control" />
        <label>Cat Id</label>
        <input type="text" v-model="cat_name" required class="form-control" />
        <label>Cat name</label>
        <br />
        <button type="submit" class="btn btn-primary">Submit</button>
        <router-link
          v-bind:to="{
            name: 'admin',
          }"
          class="btn btn-warning"
        >
          Cancel
        </router-link>
      </form>
    </div>
  </div>
</template>

<script>
import db from "@/firebaseConfig.js";
export default {
  name: "new-cat",
  data() {
    return {
      cat_id: null,
      cat_name: null,
    };
  },
  methods: {
    saveCat() {
      db.collection("categories")
        .add({
          cat_id: this.cat_id,
          name: this.cat_name,
        })
        .then((docRef) => this.$router.push("/admin"))
        .catch((error) => console.log(err));
    },
  },
};
</script>
